.header{
    margin-top: 60px;
}

.headerTitles{
    display: flex;
    align-items: center;
    flex-direction: column;
    font-family: 'Lora',serif;
    color: #444;
}

.headerTitleSm{
    position: absolute;
    top: 18%;
    font-size: 20px;
}

.headerTitleLg{
    position: absolute;
    top: 20%;
    font-size: 100px;
}

.headerImage{
    width: 100%;
    height: 550px;
    margin-top: 40px;
    object-fit: cover;
}
